body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.1rem;
}

nav {
  display: flex;
  padding: 2rem 0;
  background: #111;
}

nav section {
  width: 100%;
}

nav a {
  font-weight: 700;
  padding: 1rem 1.5rem;
  border-radius: 4px;
}

nav a:first-of-type {
  margin-left: -1.5rem;
}

nav a:hover {
  background: #222;
}

a {
  color: #ea3546;
  text-decoration: none;
}

section {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1.5rem;
}

section h1 {
  font-size: 3rem;
}

input,
select,
textarea {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

textarea {
  width: 100%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}

.question h2 {
  font-size: 2.5rem;
}

.question-excerpt {
  padding: 1.5rem 0;
  border-bottom: 1px solid #eee;
}

.question-excerpt:first-of-type {
  padding-top: 0;
}

.question-excerpt h2 {
  margin-top: 0;
  font-size: 1.5rem;
}

.answers {
  background: #f0f0f0;
  border-radius: 4px;
  padding: 1% 1% 2% 2%;
  margin: 1rem 0;
}

.answer {
  width: 90%;
  margin-right: 2%;
}

.button {
  display: inline-block;
  background: #ea3546;
  color: white;
  border-radius: 4px;
  font-weight: 700;
  padding: 0.75rem 1.5rem;
}

.button:hover{
  color: white;
}

.right {
  float: right;
}

.footer {
  margin: auto;
  width: 80%;
  position: relative;
  margin: 1% 9%;
}

.footer hr {
  color: rgb(122, 118, 118);
}

img {
  margin: 0px 5px;
  cursor: pointer;
}

.parte-votacion {
  text-align: right;
}

.parte-votacion img {
  margin: 1px;
  margin-top: 2px;
}

.parte-votacion img:hover{
  width: 36px;
  height: 36px;
}

.parte-votacion img:active{
  filter: hue-rotate(120deg);
}

.posicion{
  padding: 4% 0;
}

.background-login{
  background-color: rgba(56, 69, 144, 0.24);
}